import './main-home.scss';
import { Component } from 'react';
import ImgHomeDesktop from '../../assets/bandeaudesktop.png';
import ImgHomeMobile from '../../assets/bandeauMobile.png';
import Bandeau from '../../components/Bandeau';
import ContainerCards from '../../components/ContainerCards'

/**
 * Component Main of Home's page
 * 
 * @return DOM of main
 */
class Home extends Component {

  render() {
    const props = {
      page: "Home",
      text: "Chez vous, partout et ailleurs",
      img: {
        desktop: ImgHomeDesktop,
        mobile: ImgHomeMobile
      }
    }

    return (
      <main className="container-main">
        <div className="content-main">
          <Bandeau {...props} />
            <ContainerCards />

        </div>
      </main>
    );
  }
}

export default Home;
