import { Component } from 'react'
/* import ImgHomeDesktop from '../../assets/bandeaudesktop.png'
import ImgHomeMobile from '../../assets/bandeauMobile.png' */
import './main-bandeau.scss'

/**
 * Send Dom bandeau
 * @return { Dom }
 */
class Bandeau extends Component {

    render() {
        return (
            <div className="bandeau-container">
                <picture className="bandeau-content">
                    <source srcSet={this.props.img.desktop} media="(min-width: 768px)" />
                    <img className="bandeau-content__img" src={this.props.img.mobile} alt=" ReprÃ©sentation d'une falaise qui surplombe la mer avec la vue sur des montagnes en fond" />
                    <h1 className="bandeau-content__text">{this.props.text}</h1>
                </picture>
            </div>
        )
    }
}

export default Bandeau;
